import {getDate} from "./layout";


const Footer = ({ footerClass}) => (
      <div className={`defaultFooter ${footerClass}`}>
          <p>{meta.copyright + " " + getDate()}</p>
      </div>
);

Footer.propTypes = {
    footerClass: PropTypes.string,
};

export default Footer;
